import ePub from 'epubjs/dist/epub.min.js'
import {ImgUrl} from '@/common/common.js'
import Header from '@/components/header/header.vue'
import {get,post} from '@/common/methods.js'
export default{
	data(){
		return {
			title: "",
			book: "",
			rendition:"",
			showleft: false,
			showright: true,
			navigation :"",
			locations: "",
			showmulu: false,
			booktitle: "",
			bookuser: "",
			bookimg: "",
			showColor: "#F6F4EC",
			showsets: false,
			showfont: "Simsun",
			fontsize:  18,
		}
	},
	onLoad(e){
		this.book = new ePub(ImgUrl + e.bookpath);
		this.rendition = this.book.renderTo('books', {
			width: window.innerWidth-20,
			height: window.innerHeight-100,
			method: 'default'
		})
		this.rendition.display()
	},
	onReady(){
		this.rendition.themes.fontSize("18px");
		this.rendition.themes.font("宋体");
		this.book.ready.then(() => {
			// 生成目录
			this.navigation = this.book.navigation.toc
			// 生成Locations对象
			return this.book.locations.generate()
		  }).then(result => {
			// 保存locations对象
			this.locations = this.book.locations
			// 标记电子书为解析完毕状态
		});
		this.book.loaded.metadata.then((meta)=> {
			this.booktitle = meta.title;
			this.bookuser = meta.creator;
			if (this.book.archive) {
				this.book.archive.createUrl(this.book.cover).then((url)=> {
					this.bookimg = url;
				})
			} else {
				this.bookimg = this.book.cover;
			}
		});
		
	},
	methods: {
		prevPage() {
			if (this.rendition) {
			  this.rendition.prev()
			}
		},
		nextPage() {
			if (this.rendition) {
				this.showleft = true;
				this.rendition.next()
			}
		},
		changemulu(){
			this.showsets = false;
			this.showmulu = !this.showmulu;
		},
		changesets(){
			this.showmulu = false;
			this.showsets = !this.showsets;
		},
		closeBtn(){
			this.showsets = false;
		},
		goBack(){
			uni.navigateBack({
				delta: 1
			})
		},
		changeColor(num){
			this.showColor = num;
			this.$refs.b_content.$el.style.backgroundColor = num;
		},
		changeFont(name){
			this.showfont = name;
			this.rendition.themes.font(name);
		},
		changeFonts(type){
			if(type == 0){
				this.fontsize = this.fontsize -1;
			}else{
				this.fontsize = this.fontsize +1;
			}
			this.rendition.themes.fontSize(this.fontsize+"px");
		},
		
		
	},
	components:{
		Header
	}
}
